<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-input  placeholder="" v-model="findOrderId"></el-input>
				</el-form-item>
			<!-- 	<el-form-item label="">
					<el-select placeholder="">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
				</el-form-item> -->
				<el-form-item label="">
					<el-col :span="11">
						 <el-date-picker v-model="time" type="daterange" align="right" placeholder="" :picker-options="pickerOptions">
    </el-date-picker>
					</el-col>
					<el-col class="line" :span="2">-</el-col>
					<el-col :span="11">
						<!-- <el-time-picker type="fixed-time" placeholder=""  style="width: 100%;"></el-time-picker> -->
					</el-col>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="search"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="orders" highlight-current-row v-loading="listLoading"  @selection-change="selsChange" style="width: 100%;">	<el-table-column type="expand">
      	<template scope="props">
        	<el-form label-position="left" inline class="demo-table-expand">
          	<el-form-item label=":">
            	<span>{{ props.row.id }}</span>
          	</el-form-item><br />
          	<el-form-item label=":">
            	<span>{{ props.row.status }}</span>
          	</el-form-item><br>
          	<el-form-item label=":">
            	<span>{{ props.row.createTime | time}}</span>
          	</el-form-item><br />
          	<el-form-item label=":">
            	<span>{{ props.row.payTime | time}}</span>
          	</el-form-item><br />
          	<el-form-item label=":">
            	<span>{{ props.row.status | showStatue}}</span>
          	</el-form-item><br />
          	<el-form-item label="id:">
            	<span>{{ props.row.userId }}</span>
          	</el-form-item><br />
          	<el-form-item label="">
            	<span>{{ props.row.amount }}</span>
          	</el-form-item><br />
          	<el-form-item label="">
            	<span>{{ props.row.param }}</span>
          	</el-form-item><br />
          	<el-form-item label="id">
            	<span>{{ props.row.goodsId }}</span>
          	</el-form-item><br />	
          	<el-form-item label="url">
            	<span>{{ props.row.url }}</span>
          	</el-form-item>
        	</el-form>
      	</template>
    	</el-table-column>
			<el-table-column type="id" prop="id"   label="">
			</el-table-column>
			<el-table-column label="icon"  sortable>
		 			<template scope="scope"> 
		 				<img v-bind:src="scope.row.playerInfo?scope.row.playerInfo.wxHeadimgurl:null" class="image" style="    width:50px;height:50px;border-radius:50px" />
		 			</template>
			</el-table-column>
			<el-table-column prop="playerInfo.wxNickname" label=""  sortable>
			</el-table-column>
			<el-table-column prop="userId" label="id"  sortable>
			</el-table-column>
			<el-table-column prop="amount" label=""   sortable> <!--  :formatter="formatSex" -->
			</el-table-column>
			<el-table-column prop="createTime" label="" :formatter="showTime" sortable>
			</el-table-column>
			<el-table-column prop="payTime" label="" :formatter="showTime" sortable>
			</el-table-column>	
		<!-- 	<el-table-column prop="param" label="" sortable>
			</el-table-column>	 -->
			<el-table-column prop="status" label="" sortable>
			</el-table-column>
			<el-table-column label="">
				<template scope="scope">
					<!-- <el-button size="small" @click="selectRow(scope.$index, scope.row)"></el-button> -->
					<el-button type="info" size="small" @click="selectRow(scope.row.id,scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			 <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> 
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pageShow" :total="total" :current-page="page" style="float:right;">
			</el-pagination>
		</el-col>


	</section>
</template>

<script>
	import util from '../../common/js/util'
	import { mapState, mapActions } from 'vuex'
	export default {
		data() {
			return {
				findOrderId:"",
				sels:[],
				time:"",
				pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
			}
			
		},
		computed: {
   		 ...mapState({
    		 		listLoading:state => state.orderManager.listLoading,
    		 		orders:state=>state.orderManager.orders,
    		 		total:state=>state.orderManager.total,
    		 		page:state=>state.orderManager.page,
    		 		pageShow:state=>state.orderManager.pageShow,
    		 })
  	},
  	filters:{
  		time:util.formatDate.formatter,
  		showStatue:transForShow
  	},
		methods: {
			//
			...mapActions([
				'getOrderList',
				'findOrderById',
			]),
			search () {
					if(this.findOrderId.length == 0) {
					return this.getOrderList()
				}
				this.$store.dispatch("findOrderById",this.findOrderId)
			},
			handleCurrentChange(val) {
				
			},
			async selectRow(id,playerInfo) {
				this.$message("")
				//await this.setPlayerDetails({id,playerInfo})
				//this.$router.replace('/playerDetail')
			},
			selsChange: function (sels) {
			},
			showTime:function (row,cloumn) {
				let c = cloumn.property
				return util.formatDate.formatter(row[c])
			},
			//
			batchRemove: function () {
			}
		},
		mounted() {
			this.getOrderList()
		}
	}
function transForShow (num) {
  /*
    :
    -3:
    -2 
    -1,
     0;
     2
     1;
  */
  switch (num) {
    case 0 :
      return ""
    case 1 :
      return ""
    case -1 :
      return ""
    default :
      return ':' + num
  }
}
</script>

<style scoped>

</style>


