<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-input  placeholder="" v-model="findApplyId"></el-input>
				</el-form-item>
			<!-- 	<el-form-item label="">
					<el-select placeholder="">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
				</el-form-item> -->
				<el-form-item label="">
					<el-col :span="11">
						 <el-date-picker v-model="timeScope" type="daterange" align="right" placeholder="()" :picker-options="pickerOptions">
    </el-date-picker>
					</el-col>
					<el-col class="line" :span="2">-</el-col>
					<el-col :span="11">
						<!-- <el-time-picker type="fixed-time" placeholder=""  style="width: 100%;"></el-time-picker> -->
					</el-col>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="search"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="applyList" highlight-current-row v-loading="listLoading"  @selection-change="selsChange" style="width: 100%;">
			<!-- <el-table-column type="selection">
			</el-table-column> -->
			<el-table-column type="expand">
      	<template scope="props">
        	<el-form label-position="left" inline class="demo-table-expand">
          	<el-form-item label=":">
            	<span>{{ props.row.id }}</span>
          	</el-form-item>
          	<el-form-item label=":">
            	<span>{{ props.row.cash }}</span>
          	</el-form-item><br>
          	<el-form-item label=":">
            	<span>{{ props.row.createTime | time}}</span>
          	</el-form-item>
          	<el-form-item label=":">
            	<span>{{ props.row.endTime | time}}</span>
          	</el-form-item>
          	<el-form-item label=":">
            	<span>{{ props.row.status | showStatue}}</span>
          	</el-form-item><br />
          	<el-form-item label="id:">
            	<span>{{ props.row.sellerId }}</span>
          	</el-form-item><br />
          	<el-form-item label=":">
            	<span>{{ props.row.params }}</span>
          	</el-form-item>
          	<el-form-item label="">
            	<span>{{ props.row.description }}</span>
          	</el-form-item><br />
          	<div v-for="item in props.row.RebateDetail" >
          		<el-form-item label="">
            		<span>{{ item.orderId}}</span>
          		</el-form-item>
          		<el-form-item label="">
            		<span>{{ item.cash}}</span>
          		</el-form-item>
          		<br />	
          	</div>
        	</el-form>
      	</template>
    	</el-table-column>
			<el-table-column type="id" prop="id"  label="" >
			</el-table-column>
			<el-table-column label="icon"  sortable>
		 				<template scope="scope"> <img v-bind:src="scope.row.seller.wxInfo.wxHeadimgurl" class="image" style="    width:50px;height:50px;border-radius:50px" /></template>
			</el-table-column>
			<el-table-column prop="seller.wxInfo.wxNickname" label=""   sortable> <!--  :formatter="formatSex" -->
			</el-table-column>
			<el-table-column prop="createTime" label="" :formatter="showTime" sortable>
			</el-table-column>
			<el-table-column prop="cash" label="" sortable>
			</el-table-column>	
			<el-table-column prop="status" label="" :formatter="showStatue" sortable>
			</el-table-column>
			<el-table-column label="">
				<template scope="scope">
					<!-- <el-button size="small" @click="selectRow(scope.$index, scope.row)"></el-button> -->
					<el-button type="info" size="small" v-show= "scope.row.status == 0" @click="selectRow(scope.row.id,scope.row,1)"></el-button>
					<el-button type="info" size="small"  v-show="scope.row.status == 0" @click="selectRow(scope.row.id,scope.row,2)"></el-button>
					<!-- <el-button type="danger" size="small"  v-show="scope.row.status <= -2" @click="selectRow(scope.row.id,scope.row,3)"></el-button> -->
				</template>
			</el-table-column>
		</el-table>
		<!---->
		<el-col :span="24" class="toolbar">
			 <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> 
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pageShow" :total="total" :current-page="page" style="float:right;">
			</el-pagination>
		</el-col>


	</section>
</template>

<script>
	import util from '../../common/js/util'
	import { mapState, mapActions } from 'vuex'
	export default {
		data() {
			return {
				findApplyId:"",
				sels:[],
				timeScope:"",
				pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
			}
		},
		computed: {
   		 ...mapState({
    		 		listLoading:state => state.applyManager.listLoading,
    		 		applyList:state=>state.applyManager.applyList,
    		 		total:state=>state.applyManager.total,
    		 		page:state=>state.applyManager.page,
    		 		pageShow:state=>state.applyManager.pageShow,
    		 })
  	},
  	filters: {
  		showStatue : transForShow,
  		time:util.formatDate.formatter
  	},
		methods: {
			//
			...mapActions([
				'getApplyList',
				'findApplyById',
				'applyCashById'
			]),
			search () {
				if(this.findApplyId.length == 0) {
					return this.getApplyList()
				}
				this.$store.dispatch("findApplyById",this.findApplyId)
			},
			handleCurrentChange(val) {
				
			},
			async selectRow(id,row,num) {
				// num = 1 
				// num = 2 
				let self = this
				if(num !== 1 && num !== 2) {
					return this.$message({message:'',type: 'error'});
				}
				if(row.status === num) {
					this.$message({message:'',type: 'error'});
					return
				}
				let res = await this.applyCashById({id,num})
				if(res && res.isSucc >= 0) {
					this.$message({message:'',type: 'success'});
				}else{
					this.$message({message:res.des||'',type: 'error'});
				}
				return this.getApplyList()
			},
			selsChange: function (sels) {
			},
			showStatue:function(row) {
				return transForShow(row.status)
			},
			showTime:function (row,cloumn) {
				let c = cloumn.property
				return util.formatDate.formatter(row[c])
			},
			//
			batchRemove: function () {
				return 
			}
		},
		async created() {
		},
		async mounted() {
			await this.getApplyList()
		}
	}

function transForShow (num) {
  /*
    :
    -3:
    -2 
    -1,
     0;
     2
     1;
  */
  switch (num) {
    case -3 :
      return ""
    case -2 :
      return ""
    case -1 :
      return ""
    case 0 :
      return ""
    case 1 :
      return ""
    case 2 :
      return ""
    default :
      return ':' + num
  }
}
</script>

<style scoped>

</style>


