<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-input  placeholder="id" v-model="findId"></el-input>
				</el-form-item>
			<!-- 	<el-form-item label="">
					<el-select placeholder="">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="11">
						<el-date-picker type="date" placeholder=""  style="width: 100%;"></el-date-picker>
					</el-col>
					<el-col class="line" :span="2">-</el-col>
					<el-col :span="11">
						<el-time-picker type="fixed-time" placeholder=""  style="width: 100%;"></el-time-picker>
					</el-col>
				</el-form-item> -->
				<el-form-item>
					<el-button type="primary" v-on:click="search"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="newQuestion"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="questions" highlight-current-row v-loading="listLoading" style="width: 100%;">
			<el-table-column type="selection">
			</el-table-column>
			<el-table-column type="id" prop="id"  label="id">
			</el-table-column>
			<el-table-column prop="name" label=""sortable>
			</el-table-column>
			<el-table-column prop="status" label="" sortable>
			</el-table-column>
			<el-table-column prop="upTime" label="" sortable>
			</el-table-column>
			<el-table-column label="">
				<template scope="scope">
					<!-- <el-button size="small" @click="selectRow(scope.$index, scope.row)"></el-button> -->
					<el-button type="info" size="small" @click="selectRow"></el-button>
					<el-button type="info" size="small" @click="public"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pageShow" :total="total" :current-page="page" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.sex">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="editForm.age" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="editForm.birth"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editForm.addr"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>
		<el-dialog title="" v-model="dialogVisible" size="tiny">
  				<div id="erweima">
  				{{dizhi}}
  				</div>
  			<span slot="footer" class="dialog-footer">
    		<el-button @click="dialogVisible = false"> </el-button>
    		<el-button type="primary" @click="dialogVisible = false"> </el-button>
  </span>
</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	import { mapState, mapActions } from 'vuex'
	export default {
		data() {
			return {
				dialogVisible:false,
				dizhi:"",
				findId:undefined,
				sels: [],//
				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id: 0,
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				}

			}
		},
		computed: {
   		 ...mapState({
    		 	listLoading:state => state.question.listLoading,
    		 	questions:state=>state.question.questions,
    		 	total:state=>state.question.total,
    		 	page:state=>state.question.page,
    		 	pageShow:state=>state.question.pageShow
    		 })
  	},
		methods: {
			//
			...mapActions([
				"getQuestion",
				"setQuestionId"
			]),
			search () {
				this.$store.dispatch("findById",this.findId)
			},
			handleCurrentChange(){

			},
			async selectRow(row) {
				await this.setQuestionId(row.id)
				this.$router.replace('/newQuestion')
			},
			newQuestion() {
				this.$router.replace('/newQuestion')
			},
			public(id){
			this.dizhi = "http://60.205.205.60:"
			this.dialogVisible = true
			},
			handleDel: function (index, row) {
			 this.$message('')
			 return
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
			this.$message('')
			 return
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.$message('')
			 	return
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				};
			},
			//
			editSubmit: function () {
			this.$message('')
			 return
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
			this.$message('')
			 return
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				//this.sels = sels;
			},
			//
			batchRemove: function () {
				return 
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getQuestion()
		}
	}

</script>

<style scoped>

</style>


