<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-input  placeholder="" v-model="findRoomId"></el-input>
				</el-form-item>
			<!-- 	<el-form-item label="">
					<el-select placeholder="">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
				</el-form-item> -->
				<el-form-item label="">
					<el-col :span="11">
						 <el-date-picker v-model="time" type="daterange" align="right" placeholder="" :picker-options="pickerOptions">
    </el-date-picker>
					</el-col>
					<el-col class="line" :span="2">-</el-col>
					<el-col :span="11">
						<!-- <el-time-picker type="fixed-time" placeholder=""  style="width: 100%;"></el-time-picker> -->
					</el-col>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="search"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="rooms" highlight-current-row v-loading="listLoading"  @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection">
			</el-table-column>
			<el-table-column type="id" prop="id"  label="" >
			</el-table-column>
			<el-table-column prop="creator" label=""  sortable>
			</el-table-column>
			<el-table-column prop="base_info" label=""   sortable> <!--  :formatter="formatSex" -->
			</el-table-column>
			<el-table-column prop="num_of_turns" label="" sortable>
			</el-table-column>
			<el-table-column prop="next_button" label=""  sortable>
			</el-table-column>
			<el-table-column prop="ip" label="" sortable>
			</el-table-column>	
			<el-table-column prop="port" label="" sortable>
			</el-table-column>
			<el-table-column label="">
				<template scope="scope">
					<!-- <el-button size="small" @click="selectRow(scope.$index, scope.row)"></el-button> -->
					<el-button type="info" size="small" @click="selectRow(scope.row.id,scope.row)"></el-button>
					<el-button type="info" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
					<el-button type="info" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			 <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> 
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pageShow" :total="total" :current-page="page" style="float:right;">
			</el-pagination>
		</el-col>


	</section>
</template>

<script>
	import util from '../../common/js/util'
	import { mapState, mapActions } from 'vuex'
	export default {
		data() {
			return {
				findRoomId:"",
				sels:[],
				time:"",
				pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
			}
			
		},
		computed: {
   		 ...mapState({
    		 		listLoading:state => state.roomManager.listLoading,
    		 		rooms:state=>state.roomManager.rooms,
    		 		total:state=>state.roomManager.total,
    		 		page:state=>state.roomManager.page,
    		 		pageShow:state=>state.roomManager.pageShow,
    		 })
  	},
		methods: {
			//
			...mapActions([
				'getRoomList',
				'findRoomById',
			]),
			search () {
				console.log(this.time)
				return 
				this.$store.dispatch("findRoomById",this.findRoomId)
			},
			handleCurrentChange(val) {
				
			},
			async selectRow(id,playerInfo) {
				//await this.setPlayerDetails({id,playerInfo})
				//this.$router.replace('/playerDetail')
			},
			selsChange: function (sels) {
			},
			//
			batchRemove: function () {
				return 
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getRoomList()
		}
	}

</script>

<style scoped>

</style>


